import React, { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { Loading, SectionTitle, Launcher } from "components";

const Home = (props) => {
  const [loading] = useState(false);

  if (!!loading) {
    return <Loading />;
  }

  return (
    <Fragment>
      <Launcher logo="logo192.png"/>
    </Fragment>
  );
};

export default Home;
